import { Component, Input } from '@angular/core';

export interface IProjectData  {
  title: string;
  description: string;
  fields: { [key: string]: string };
  mainImage: string;
  openLink: string;
}

@Component({
  selector: 'app-project-link',
  templateUrl: './project-link.component.html',
  styleUrls: ['./project-link.component.scss']
})
export class ProjectLinkComponent {
  @Input() gameData!: IProjectData;

  get keys () {
    if (!this.gameData) return [];
    return Object.keys(this.gameData.fields);
  }

  get values () {
    if (!this.gameData) return [];
    return Object.values(this.gameData.fields);
  }

  get styles () {
    return {
      'background-image': `url(${this.gameData.mainImage})`
    }
  }
}
