import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IProjectData } from '../../components/project-link/project-link.component';

@Component({
  selector: 'app-games',
  templateUrl: './games.component.html',
  styleUrls: ['./games.component.scss']
})

export class GamesComponent implements OnInit {
  public gameList: IProjectData[] = []

  constructor (private route: ActivatedRoute) {}

  ngOnInit () {
    this.route.data.subscribe(({ data }) => {
      this.gameList = data as IProjectData[];
      this.gameList.sort((a, b) => parseInt(b.fields.Year) - parseInt(a.fields.Year));
    });
  }
}
