import { HttpClient } from '@angular/common/http';
import { Injectable } from "@angular/core";
import { Observable } from 'rxjs';
import { IProjectData } from '../components/project-link/project-link.component';

@Injectable({
  providedIn: 'root'
})

export class DBService {
  constructor (
    private http: HttpClient
  ) {}

  /**
   * Returns games list
   */
  getGamesList(): Observable<IProjectData[]> {
    return this.http.get('/assets/db/games.json') as Observable<IProjectData[]>;
  }


  /**
   * Returns projects list
   */
  getProjectsList(): Observable<IProjectData[]> {
    return this.http.get('/assets/db/projects.json') as Observable<IProjectData[]>;
  }
}
