import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IProjectData } from 'src/app/components/project-link/project-link.component';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {
  projectList: IProjectData[] = []
  constructor(
    private route: ActivatedRoute
  ) { }

  ngOnInit () {
    this.route.data.subscribe(({ data }) => {
      this.projectList = data as IProjectData[];
      this.projectList.sort((a, b) => parseInt(b.fields.Year) - parseInt(a.fields.Year));
    });
  }
}
